
.header {
  position: relative;
  color: #fff;
  background-color: rgba(7,17,27,0.5);
  line-height: 12px;
  font-size: 12px;
}
.header .content-wraper {
  padding: 24px 12px 18px 24px;
  font-size: 0;
}
.header .content-wraper .avatar {
  display: inline-block;
  vertical-align: top;
}
.header .content-wraper .avatar img {
  border-radius: 2px;
}
.header .content-wraper .content {
  display: inline-block;
  margin-left: 16px;
  font-size: 14px;
}
.header .content-wraper .content .title {
  margin: 2px 0 8px 0;
}
.header .content-wraper .content .title .brand {
  display: inline-block;
  width: 30px;
  height: 18px;
  vertical-align: bottom;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wraper .content .title .brand {
    background-image: url("brand@3x.png");
}
}
.header .content-wraper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  font-weight: bold;
  line-height: 8px;
}
.header .content-wraper .content .description {
  margin-bottom: 10px;
}
.support .icon {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.support .decrease {
  background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.support .decrease {
    background-image: url("decrease_1@3x.png");
}
}
.support .discount {
  background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.support .discount {
    background-image: url("discount_1@3x.png");
}
}
.support .guarantee {
  background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.support .guarantee {
    background-image: url("guarantee_1@3x.png");
}
}
.support .invoice {
  background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.support .invoice {
    background-image: url("invoice_1@3x.png");
}
}
.support .special {
  background-image: url("special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.support .special {
    background-image: url("special_1@3x.png");
}
}
.support .text {
  line-height: 12px;
  font-size: 10px;
}
.support-count {
  position: absolute;
  right: 12px;
  bottom: 50px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  border-radius: 14px;
  background-color: rgba(0,0,0,0.2);
  text-align: center;
}
.support-count .count {
  vertical-align: top;
  font-size: 10px;
}
.support-count .icon-keyboard_arrow_right {
  margin-left: 2px;
  line-height: 24px;
  font-size: 10px;
}
.bulletin-wraper {
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 24px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: rgba(7,17,27,0.2);
}
.bulletin-wraper .bulletin-title {
  display: inline-block;
  vertical-align: top;
  margin-top: 7px;
  width: 22px;
  height: 12px;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.bulletin-wraper .bulletin-title {
    background-image: url("bulletin@3x.png");
}
}
.bulletin-wraper .bulletin-text {
  vertical-align: top;
  margin: 0 4px;
  font-size: 10px;
}
.bulletin-wraper .icon-keyboard_arrow_right {
  position: absolute;
  font-size: 10px;
  right: 12px;
  bottom: 8px;
}
.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(7,17,27,0.8);
}
.detail.fade-enter-active,
.detail.fade-leave-active {
  transition: all 1s;
}
.detail.fade-enter,
.detail.fade-leave-active {
  opacity: 0;
  background-color: rgba(7,17,27,0);
}
.detail .detail-wrapper {
  width: 100%;
  min-height: 100%;
}
.detail .detail-wrapper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.detail .detail-wrapper .detail-main .name {
  line-height: 16px;
