@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.detail .detail-wrapper .detail-main .support .support-item .invoice {
    background-image: url("invoice_2@3x.png");
}
}
.detail .detail-wrapper .detail-main .support .support-item .special {
  background-image: url("special_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.detail .detail-wrapper .detail-main .support .support-item .special {
    background-image: url("special_2@3x.png");
}
}
.detail .detail-wrapper .detail-main .support .support-item .text {
  line-height: 16px;
  font-size: 12px;
}
.detail .detail-wrapper .detail-main .bulletin {
  width: 80%;
  margin: 0 auto;
}
.detail .detail-wrapper .detail-main .bulletin .content {
  padding: 0 12px;
  line-height: 24px;
  font-size: 12px;
}
.detail .detail-close {
  position: absolute;
  width: 32px;
  height: 32px;
  margin: -50px auto 0 auto;
  clear: both;
  font-size: 32px;
  left: 0;
  right: 0;
}


