
.food {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: #fff;
/*  只有单一方向动画
	&.fly-enter-active{
		transform: translate3d(100%, 0, 0)
		transition: all 0.2s linear
	}
	&.fly-enter-to{
		transform: translate3d(0%, 0, 0)
	}
	*/
}
.food.fly-enter-active,
.food.fly-leave-active {
  transition: all 0.2s linear;
}
.food.fly-enter,
.food.fly-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .food-content {
  padding-bottom: 100px;
}
.food .food-content .image-header {
  position: relative;
}
.food .food-content .image-header img {
  width: 100%;
}
.food .food-content .image-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .food-content .image-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .food-content .content {
  position: relative;
  padding: 18px;
}
.food .food-content .content .title {
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  margin-bottom: 8px;
  color: #07111b;
}
.food .food-content .content .details {
  margin-bottom: 18px;
  color: #93999f;
  font-size: 10px;
  line-height: 10px;
}
.food .food-content .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food .food-content .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.food .food-content .content .price .old {
  color: #93999f;
  text-decoration: line-through;
  font-size: 10px;
}
.food .food-content .content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  top: 12px;
}
.food .food-content .content .buy {
  position: absolute;
  right: 18px;
  top: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  border-radius: 12px;
  font-size: 10px;
  color: #fff;
  background: #00a0dc;
  opacity: 1;
}
.food .food-content .content .buy.fade-enter-active,
.food .food-content .content .buy.fade-leave-active {
  transition: all 1s;
}
.food .food-content .content .buy.fade-enter,
.food .food-content .content .buy.fade-leave-active {
  opacity: 0;
  z-index: -1;
}
.food .food-content .info {
  padding: 18px;
}
.food .food-content .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food .food-content .info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: #4d555d;
}
.food .food-content .rating {
  padding-top: 18px;
}
.food .food-content .rating .title {
  line-height: 14px;
  margin-left: 6px;
  font-size: 14px;
  color: #07111b;
}
.food .food-content .rating .rating-wrapper {
  padding: 0 18px;
}
.food .food-content .rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food .food-content .rating .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.food .food-content .rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  line-height: 12px;
}
.food .food-content .rating .rating-wrapper .rating-item .user img {
  border-radius: 50%;
  margin-left: 6px;
}
.food .food-content .rating .rating-wrapper .rating-item .time {
  margin-bottom: 6px;
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
}
.food .food-content .rating .rating-wrapper .rating-item .text {
  color: #07111b;
  font-size: 12px;
  line-height: 12px;
  vertical-align: top;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_up,
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  margin-right: 4px;
  font-size: 12px;
  line-height: 24px;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.food .food-content .rating .rating-wrapper .no-ratings {
  color: #939e9f;
  font-size: 16px;
  text-align: center;
  margin-top: 10px;
}


